<template>
	<view class="infinite-list" style="overflow:auto">

		<view class="box-card item" v-for="(item,index) in newsList" :key="index"  >
			<!-- s -->
			<view class="ah-tab-content-wrapper">
				<view class="ah-tab-content ">
					<!-- 单个order-list -->
					<view class="order-list">
						<view class="order-cell weui-cell">
							<view class="order-num">
								<span decode="true">
									订&ensp;单&ensp;号：
								</span>
							</view>
							<!-- 	<view class="order-num-info">{{item.order_sn}}</view>
							<view class="order-type"> {{item.statusinfo}}</view> -->
							<view class="order-num-info">{{item.order_sn}}</view>
							<view class="order-type"> {{item.statusinfo}}</view>
						</view>
						<view class="order-des weui-line ">
							<view class="order-info ">
									<view class="weui-cell order-cell">
									<view class="order-num">订单类型：</view>
									<view>{{item.typename}}</view>
								</view>
						
									<view class="weui-cell order-cell ">
									<view class="order-num">Account：</view>
									<view>{{item.AccountNo}}</view>
								</view>
						<view class="weui-cell order-cell ">
							<view class="order-num">Identifier：</view>
							<view>{{item.Identifier}}</view>
						</view>
								
								<view class="weui-cell  order-cell ">
									<view class="order-num">订单金额：</view>
									<view>PHP{{item.price}}</view>
								</view>
								<view class="weui-cell  order-cell ">
									<view class="order-num">手续费：</view>
									<view>PHP{{item.service_price}}</view>
								</view> 
									<view class="weui-cell  order-cell ">
									<view class="order-num">订单时间：</view>
									<view>{{item.pay_time}}</view>
								</view>
							</view>
						</view>
					</view>




				</view>

				<!-- e -->
			</view>





			<!-- <view v-for="o in 4" :key="o" class="text item">
      {{'列表内容 ' + o }}
    </view> -->
		</view>


		<view id="loadMore" v-if="droping">加载中...</view>
		<view id="noMore" v-if="noMore">没有更多了</view>
	</view>





</template>




<script>
	// 导入组件


	export default {
		name: 'historycard',
		// 组件注册
		components: {

		},
		data() {
			return {
				apiUrl: 'https://ccc.wangwanglive.com/index.php/api/V1/',
				//apiUrl:'http://feic.com/index.php/api/V1/' ,

				items: '',

				newsList: [],
				moreList: [],
				i: 0,
				droping: false,
				noMore: false,
				count: 5,
				i:0,


			}
		},

		// 获得上个页面的传值,进页面自动加载一次
		onLoad(options) {
		
		uni.showToast({
		       title:'加载中...',  
			   duration:1500,
		       icon:"none"  
		         }); 
			  
			this.loadMore();
			console.log(options)
			console.log(options.params)
			// this.loadMore();
		},
		// 页面滚动到底部的事件

		onReachBottom() {
			
			var _this=this;
	
	  _this.loadMore()

		},






		methods: {
			noM() {
				return this.count >= 30
			},
		
			loadMore() { 
			 let _this = this;
				// 无法获取真实数据,改用静态数据书写样式以及触底加载
				this.i += 1;
			    uni.request({
			    	url: 
			    		// "@/static/data/data2.json",
			    	"https://ccc.wangwanglive.com/index.php/api/V1/api_Lifepaymentlist", //仅为示例，并非真实接口地址。
			    	method: 'GET',
			    	data: {
			    		 "id": uni.getStorageSync('userlist').user_id,
						 "page": _this.i, 
						 "pageSize": 8
			    	
			    	}, 
			    	// header: {
			    	//     'custom-header': 'hello' //自定义请求头信息
			    	// }, 
			    	success: (res) => {
							// this.$emit('fun', false)
							   _this.moreList=res.data.data;
							   	console.log(_this.moreList);
						  if(_this.moreList.length == 0 ){
							     _this.noMore = true;
							     _this.droping = false;
							          }else{
								 _this.droping = true;
								 _this.noMore = false;		  
					 _this.moreList.forEach(function(item){
					                _this.newsList.push(item);
					              })
								  }
						  if(_this.moreList.length<8){
						  					  _this.noMore = true;
						  					  _this.droping = false;
						  					  
						  }
						  					
						  
						//console.log(_this.newsList);
			    			
			    	},
					
				  });
			
				//_this.droping = false
			
			}
			
		},
	

	


	}





	
</script>


<style scoped>
	* {
		font-size: 14px !important;
	}

	.text {
		font-size: 28rpx;
	}




	.box-card {
		margin-bottom: 5px;
	}

	.order-list>view.order-cell:first-child {
		font-weight: 700;
	}





	.order-list {
		margin-bottom: 10rpx;
		border-radius: 30rpx;
		padding: 20rpx;
		background-color: #fff;
		overflow: hidden;
		margin-top: 20rpx;
	}

	.order-info {
		font-weight: 400;
		color: #999
	}



	.order-des {
		position: relative
	}

	.weui-cell {

		display: -webkit-box;
		display: -webkit-flex;
		display: flex;
		-webkit-box-align: center;
		-webkit-align-items: center;
		align-items: center;
		font-weight: 400;
		line-height: 2em;
		height: 2em;


		position: relative
	}

	.order-cell .order-num {
		width: 25%;
		padding-left: 0;
		text-align: center;

	}




	.order-cell .order-type {
		color: #e4393c;
		font-size: 24rpx;

	}

	.order-cell .order-num-info {
		text-align: left;
		font-size: 28rpx;
		width: 60%;
		overflow: hidden;
		text-overflow: ellipsis;

	}

	.empty {
		padding: 60rpx 20rpx;
		margin: 0 auto;
		text-align: center
	}

	.empty:after {
		content: "'没有更多内容'";
		text-align: center;
		color: #e43;
		font-size: inherit;
		font-size: .186667rem
	}

	.infinite-list {
		/* #ifdef H5 */
		
		/* height: 100vh; */
		/* #endif */
		background-color: #f5f5f5;

	}
	#loadMore{
		line-height: 1.5em;
		height: 1.5em;
		text-align: center;
	}
</style>
